.newsletter {
    box-sizing: border-box;
    width: 100%;
    min-height: 250px;
    background-color: #f5f5f5;
    color: #000;
    display: flex;
    align-items: center;
    padding: 25px 50px;
}

.newsletter__form {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    align-items: center;
}

.newsletter__label {
    font-family: "Miller Display";
    font-size: 36px;
    font-style: italic;
    letter-spacing: 0.05em;
    text-align: center;
}

.newsletter__group {
    display: flex;
    flex-wrap: nowrap;
}

.newsletter__input {
    box-sizing: border-box;
    max-width: 500px;
    height: 54px;
    border: 3px solid #eeeeee;
    background-color: #ffffff;
    opacity: 0.4;
    font-size: 18px;
    font-weight: 500;
    letter-spacing: 0.9px;
    padding-left: 10px
}

.newsletter__button {
    min-width: 150px;
    height: 54px;
    border: 3px solid #000000;
    background-color: #ffffff;
    font-size: 18px;
    font-weight: 500;
    letter-spacing: 0.05em;
}

.newsletter__button:hover {
    cursor: pointer;
    background-color: transparent;
    border-color:#eeeeee;
}

@media screen and (max-width: 768px) {
    .newsletter__form {
        justify-content: center;
    }
    .newsletter__label {
        font-size: 24px;
    }
    .newsletter__group {
        flex-wrap: wrap;
        justify-content: center;
    }
    .newsletter__button,
    .newsletter__input {
        width: 80%;
    }
}